body{
    background: #E5E5E5;
    font-family: OpenSans;
}


.search-container{
    display: inline-block;
    background: white;
    border-radius: 3px;
    position: relative;
    top: -1.5vh;
    width: 63vw;
    left: 0vw;
}

.search-container input{

    width: 90%;
    position: relative;
    top: -1px;
    left: 2.5vw;
    border: none;
}

.search-container input:focus-visible{

    outline: auto #00aaff;
    top: -0.31vh;
}

.app-name{
    color: white;
    cursor: pointer;
}


.user-name{
    height: 28px;
    width: 28px;
    padding: 8px;
    background: #FFDEAC;
    border-radius: 13px;
    cursor: pointer;
}

.user-name span{

    position: relative;
    bottom: .8vh;
}

.makeStyles-paper-9 {
    color: rgba(0, 0, 0, 0.54);
    border-radius: 0;
    padding: 12px 5vw;
    display: flex;
    background: #5F22E0;
    text-align: center;
    align-items: center;
    justify-content: space-between;
}

.right-nav-wrap{
    display: flex;
    align-items: center;
    /* visibility: hidden; */
    button{
        color: #efefef;
        border: 1px solid #efefef;
        border-radius: 5px !important;
        padding: 4px 5px !important;
        &:hover{
          border: 1px solid #ffffff;
        }
    }
}

.logout-btn{
    color: white !important;
    margin-left: 20px;
    cursor: pointer;
}
a{
    color: white;
    text-decoration: none;
    &:hover{
        color: white;
    }
}