.landing-wrapper-courses {
    width     : 100%;
    background: white;
    max-width : 1120px;
    margin    : 20px auto;
    overflow  : hidden;
    padding   : 20px 20px;
}

.courses-navbar{
    ul{
        list-style: none;
        display: flex;
        justify-content: flex-start;
        li{
            width: 200px;
            text-align: center;
            cursor: pointer;
            padding-bottom: 10px;
            border-bottom: 4px solid #fff;
        }
        .active{
            border-bottom: 4px solid orange;
        }
    }
}

.courses-loop{
    display: flex;
    flex-wrap: wrap;
}

.left-filters{
    margin-right: 1rem;
    height: 900px;
    padding: 15px;
}

.btn-wrap{
    display: flex;
    flex-wrap: wrap;
    button{
        font-size: 12px;
        text-transform: initial;
        border: 1px solid #5f22e0;
        color: #666666;
        font-family: OpenSans;
        margin: 5px 5px 2px 0px;
        padding: 5px 8px;
        border-radius: 12px;
    }
}

.MuiInputLabel-formControl{
    font-size: 12px !important;
    transform: translate(0, -18px) scale(1) !important;
    font-weight: 100;
    transform-origin: top left;
    transition: color 200ms cubic-bezier(0.0, 0, 0.2, 1) 0ms,transform 200ms cubic-bezier(0.0, 0, 0.2, 1) 0ms;
}

.section{
    padding: 30px 0;
    border-bottom: 1px solid #efefef;
}

ul{
    li{
        a{
            color: #444;
            &:hover{
                color: black;
            }
        }
    }
}

@media(max-width:720px) {
    .courses-wrapper{
        padding: 10px !important;
    }
}

.story-sm{
 .careerStories-card-wrapper{
    min-width: 170px !important;
    height: 216px !important;
    img{
        height: 125px !important;
    }
 }
}


.videos-sm{
    .videos-card-wrapper{
        height: 130px;
        min-width: 210px !important;
    }
}