.college-card-wrapper-2 {
    width              : 220px !important;
    border-radius      : 12px;
    height             : 180px;
    margin             : 0px 20px 2px 5px;
    background-position: center;
    background-size    : cover;
    background-repeat  : no-repeat;
    color              : white;
    display            : flex;
    flex-direction     : column;
    justify-content    : flex-start;
    box-shadow         : 0 0 3px grey;
    overflow           : hidden;
    cursor: pointer;

    .college-card-head {
        img {
            width : 240px;
            height: auto;
        }
    }

    .college-card-footer {
        height : 80px;
        color  : black;
        padding: 5px 8px;

        .college-card-end {
            display        : flex;
            justify-content: space-between;
            align-items    : flex-end;
            font-size      : 14px;

            span {
                font-size: 12px;
                color    : #5F22E0;
            }

            p {
                font-size: 12px;
                color    : #666666;
            }
        }
    }
}