@import '../src/Assets/Styles/utility.scss';
@import '../src/Assets/Styles/common.scss';



h1,h2,h3,h4,h5,h6,p{
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.App-logo {
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.opt-area{
  margin: 22px 0 !important;
}

/* #root{
  width: max-content;
} */

body {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.41), rgba(0, 0, 0, 0.41)), #FFFFFF;
  font-family: OpenSans;
}

@media(max-width: 650px){
  body{
    background: white !important;
    // overflow-y: hidden;
  }
  .header-img{
    img{
      height: 28vh !important;
    }
  }
  .img1{
    img{
      width: 170px !important;
      top: 5px !important;
    }
  }
  .main-body {
    width: 100vw !important;
    margin: auto;
    position: relative;
    top: 0 !important;
    padding: 0;
    margin: 0 !important;
    .card-content {
      height: 99vh;
      padding: 0;
      margin: 0;
   }
   .opt-area{
    height: 28vh;
    overflow-y: scroll;
   }
  }
  .line-one{
    span{
      font-size: 14px !important;
    }
  }
  .form-area {
    width: 90% !important;
    margin-top: 20px;
    .input-area-half {
      width: 40% !important;
      margin-bottom: 5px !important;
      float: left;
    }
  }
  .body-component {
    height: 82% !important;
  }
}

.MuiSelect-selectMenu {
  padding-left: 10px !important;
}

.text-area{
  text-align: center;
}

em{
  font-style: normal !important;
}

.PhoneInput {
  display: flex;
  align-items: center;
  /* justify-content: center; */
  width: calc(100% - 20px);
  margin: 10px auto;
}

.Mui-disabled{
  opacity: 0.7;
}

.PhoneInputInput{
  background: #F9F9FB;
  font-size: 12px;
  padding: 0 10px;
  height: 30px;
  border: 2px solid transparent !important;

  &:focus-visible {
    outline: none;
    border: none;
    border-bottom: 2px solid #5F22E0 !important;
  }
}

input:-internal-autofill-selected {
  appearance: menulist-button;
  background-image: none !important;
  background-color: -internal-light-dark(rgb(232, 240, 254), rgba(70, 90, 126, 0.4)) !important;
  color: -internal-light-dark(black, white) !important;
  border: none;
}

html { 
  scroll-behavior: smooth; 
}

.horizontal-head {
  display        : flex;
  justify-content: space-between;
  align-items    : flex-end;

  h2 {
      font-weight: bold;
      font-size  : calc(1em + 0.4vw);
  }

  h3 {
      font-weight: bold;
      font-size  : 15px;
      color      : #5F22E0;
  }
}