.internship-card{
    box-shadow: 0 0 10px #e8e8e8;
    padding: 20px;
    max-width: 710px;
    margin: 0 0 40px 20px;
    border-radius: 5px;
    .internship-head{
        display: flex;
        justify-content: space-between;
    }
}

.preference-card-sm {
    background: #FFFFFF;
    border: 1px solid #EBEBEB;
    box-sizing: border-box;
    border-radius: 26px;
    margin: 0 10px 0 0;
    padding: 7px 10px;
    font-size: 13px;
}

.points{
    display: flex;
    .item{
        margin: 5px 20px;
        .c-black{
            margin-top: 10px;
        }
    }
}