.articles-card-wrapper {
    min-width          : 250px !important;
    border-radius      : 12px;
    height             : 240px;
    margin             : 0 20px 5px 5px;
    background-position: center;
    background-size    : cover;
    background-repeat  : no-repeat;
    color              : white;
    display            : flex;
    flex-direction     : column;
    box-shadow         : 0 0 3px grey;
    overflow: hidden;

    .articles-card-head {
        img {
            width : 250px;
            height: auto;
        }
    }

    .articles-card-footer {
        height : 80px;
        color  : black;
        padding: 5px 8px;
        p{
            font-size      : 12px;
        }
        .articles-card-end {
            float: right;
            span {
                font-size  : 12px;
                color: #5F22E0;
            }
        }
    }
}