.careerStories-card-wrapper {
    min-width          : 180px !important;
    border-radius      : 12px;
    height             : 253px;
    margin             : 0 20px 5px 5px;
    background-position: center;
    background-size    : cover;
    background-repeat  : no-repeat;
    color              : white;
    display            : flex;
    flex-direction     : column;
    align-items: center;

    .careerStories-card-head {
        img {
            width: auto;
            height: 140px;
            border-radius: 50%;
        }
    }

    .careerStories-card-footer {
        height : 80px;
        color  : black;
        padding: 5px 8px;
        text-align: center;
        p{
            font-size      : 12px;
        }
        .careerStories-card-end {
            float: right;
            span {
                font-size  : 12px;
                color: #5F22E0;
            }
        }
    }
}
