.site-footer {
  background-color: #150634;
  padding         : 45px 0 20px;
  font-size       : 15px;
  line-height     : 24px;
  color           : #ffffff;
}

.site-footer hr {
  border-top-color: #bbb;
  opacity         : 0.5
}

.site-footer hr.small {
  margin: 20px 0
}

.site-footer h6 {
  color         : #fff;
  font-size     : 16px;
  text-transform: uppercase;
  margin-top    : 5px;
  letter-spacing: 2px
}

.site-footer a {
  color: #737373;
}

.site-footer a:hover {
  color          : #3366cc;
  text-decoration: none;
}

.footer-links {
  padding-left: 0;
  list-style  : none
}

.footer-links li {
  display   : block;
  margin-top: 10px;
}

.footer-links a {
  color: #efefef;
}

.footer-links a:active,
.footer-links a:focus,
.footer-links a:hover {
  color          : #3366cc;
  text-decoration: none;
}

.footer-links.inline li {
  display: inline-block
}

.site-footer .social-icons {
  text-align: left
}

.site-footer .social-icons a {
  width           : 40px;
  height          : 40px;
  line-height     : 40px;
  margin-left     : 6px;
  margin-right    : 0;
  border-radius   : 100%;
  background-color: #33353d
}

.copyright-text {
  margin: 0
}

@media (max-width:991px) {
  .site-footer [class^=col-] {
    margin-bottom: 30px
  }
}

@media (max-width:767px) {
  .site-footer {
    text-align: center;
    padding-bottom: 0;
    ul{
      p {
        span{
          margin: 0 !important;
        }
      }
    }
  }

  .site-footer .copyright-text,
  .site-footer .social-icons {
    text-align: center
  }
}

.social-icons {
  padding-left : 0;
  margin-bottom: 0;
  list-style   : none
}

.social-icons li {
  display: inline-block;
  margin : 20px 15px 10px 0;

  img {
    height: 30px;
    width : auto;
  }
}

.social-icons li.title {
  margin-right  : 15px;
  text-transform: uppercase;
  color         : #96a2b2;
  font-weight   : 700;
  font-size     : 13px
}

.social-icons a {
  background-color  : #eceeef;
  color             : #818a91;
  font-size         : 16px;
  display           : inline-block;
  line-height       : 44px;
  width             : 44px;
  height            : 44px;
  text-align        : center;
  margin-right      : 8px;
  border-radius     : 100%;
  -webkit-transition: all .2s linear;
  -o-transition     : all .2s linear;
  transition        : all .2s linear
}

.social-icons a:active,
.social-icons a:focus,
.social-icons a:hover {
  color           : #fff;
  background-color: #29aafe
}

.social-icons.size-sm a {
  line-height: 34px;
  height     : 34px;
  width      : 34px;
  font-size  : 14px
}

.social-icons a.facebook:hover {
  background-color: #3b5998
}

.social-icons a.twitter:hover {
  background-color: #00aced
}

.social-icons a.linkedin:hover {
  background-color: #007bb6
}

.social-icons a.dribbble:hover {
  background-color: #ea4c89
}

@media (max-width:767px) {
  .social-icons li.title {
    display     : block;
    margin-right: 0;
    font-weight : 600
  }
}

.jumbo-heading {
  font-size  : 40px;
  font-weight: 600;
}

.sign-wrap {
  button {
    width        : 100px;
    margin       : 23px 0px 0 -12px;
    color        : white;
    border-radius: 2px !important;
    border-color : white;
    font-size    : 14px !important;
  }
}

.more-icons {
  p {
    font-size: 16px;
    margin   : 20px 0 20px 0;

    img {
      height      : 35px;
      width       : auto;
      margin-right: 1rem;
    }
  }
}

.input-area-footer {
  width          : 100% !important;
  margin-bottom  : 0px !important;
  padding        : 2px 0 !important;
  display        : flex;
  flex-direction : column;
  justify-content: center;
  align-items    : center;
}

.quick-links{
  a{
    font-size: 18px;
  }
}