.hide{
    display: none;
}

.react-horizontal-scrolling-menu--wrapper {
    position: relative;
    .left-arrow-btn{
        position: absolute;
        top: 24%;
        z-index: 10;
        left: -24px;
        cursor: pointer;
    }
    .right-arrow-btn{
        position: absolute;
        z-index: 10;
        right: -24px;
        top: 24%;
        cursor: pointer
    }
}