.subnavbar-wrapper {
    background   : white;
    box-shadow   : 0px 2px 10px rgb(0 0 0 / 10%);
    padding      : 0;
    border-bottom: 1px solid #dededd;

    ul {
        list-style     : none;
        display        : flex;
        flex-wrap      : wrap;
        align-items    : center;
        justify-content: center;
        margin         : 0;

        li {
            margin   : 8px 25px;
            font-size: 14px;
            cursor   : pointer;
        }
    }
}

.landing-carousel {
    img {
        width : 880px;
        height: 220px;
    }
}

.preference-wrapper {
    background   : #FFFFFF;
    box-shadow   : 0px 2px 10px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding      : 10px 20px;

    .preference-card {
        background   : #FFFFFF;
        border       : 1px solid #EBEBEB;
        box-sizing   : border-box;
        border-radius: 26px;
        margin       : 0 10px 0 0;
        padding      : 8px 20px;
        font-size    : 14px;
    }
}

.landing-wrapper {
    width     : 100%;
    background: white;
    max-width : 940px;
    margin    : 20px auto;
    overflow  : hidden;
    padding   : 20px 25px;

    .horizontal-head {
        display        : flex;
        justify-content: space-between;
        align-items    : flex-end;

        h2 {
            font-weight: bold;
            font-size  : calc(1em + 0.4vw);
        }

        h3 {
            font-weight: bold;
            font-size  : 15px;
            color      : #5F22E0;
        }
    }
}



.horizontal-loop {
    display   : flex;
    overflow-x: scroll;
    max-width : 880px;
    position  : relative;
}

::-webkit-scrollbar {
    display: none;
}

.MuiPaper-elevation1 {
    box-shadow: none !important;
}

.arrow-icons {
    position: sticky;
    right   : -30px;

    svg {
        margin-top: 40px;
    }
}

.arrow-icons-left {
    position: absolute;
    left    : -30px;
    top     : -10px;

    svg {
        margin-top: 40px;
        transform : rotate(180deg);
    }
}

.scrollable {
    overflow-y: scroll;
}

@media (max-width:720px) {
    .subnavbar-wrapper ul {
        list-style     : none;
        display        : flex;
        flex-wrap      : nowrap;
        align-items    : center;
        justify-content: flex-start;
        margin         : 0;
        overflow-x     : scroll;
        padding        : 0;

        li {
            font-size  : 13px;
            cursor     : pointer;
            white-space: nowrap;
            margin     : 5px 15px;
        }
    }

    .preference-wrapper .preference-card {
        border-radius: 20px;
        margin       : 0 10px 0 0;
        padding      : 5px 15px;
        font-size    : 14px;
        white-space  : nowrap;
        font-size    : 12px;
        color        : #666666;
    }
}